@import url("https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");

.infoBox {
    flex: 1;
    cursor: pointer;
}

.infoBox:not(:last-child) {
    margin-right: 15px;
}

.infoBox__cases {
    color: #601700;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}

.infoBox--selected {
    border-top: 10px solid #5C7457;
}

.infoBox--red {
    border-color: #601700;
}

.infoBox__cases--green {
    color: #5C7457;
}

.infoBox--grey {
    border-color: #404E4D;
}

.infoBox__cases--grey {
    color: #404E4D;
}

.infoBox__total {
    color: #6c757d;
    /* Important to override Material UI rules */
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;
}