@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
.infoBox {
    flex: 1 1;
    cursor: pointer;
}

.infoBox:not(:last-child) {
    margin-right: 15px;
}

.infoBox__cases {
    color: #601700;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}

.infoBox--selected {
    border-top: 10px solid #5C7457;
}

.infoBox--red {
    border-color: #601700;
}

.infoBox__cases--green {
    color: #5C7457;
}

.infoBox--grey {
    border-color: #404E4D;
}

.infoBox__cases--grey {
    color: #404E4D;
}

.infoBox__total {
    color: #6c757d;
    /* Important to override Material UI rules */
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;
}
.map {
    height: 500px;
    background-color: white;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 16px;
    box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

.info-flag img {
    width: 100px;
    border-radius: 5px;
}

.info-flag {
    height: 80px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
}

.info-confirmed, .info-deaths, .info-recovered {
    font-size: 16px;
    margin-top: 5px;
}

.info-name {
    font-size: 20px;
    font-weight: bold;
    color: #555;
}

.table {
  margin-top: 20px;
  overflow: scroll;
  height: 400px;
  color: #6a5d5d;
  background-color: #fff;
}

.table tr {
  display: flex;
  justify-content: space-between;
}

.table td {
  padding: 0.5rem;
}
.table tr:nth-of-type(odd) {
  background-color: #f3f2f8;
}

* {
    margin: 0;
}

body {
    background-color: #F2EDEB;
    font-family: 'Noto Sans JP', sans-serif;
}

.app {
    display: flex;
    justify-content: space-evenly;
    padding: 20px;
}

.app__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.app__stats {
    display: flex;
    justify-content: space-between;
}

.app__left {
    flex: 0.9 1;
}

.app__right {
    display: flex;
    flex-direction: column;
}

.app__graphTitle {
    margin-top: 20px;
    margin-bottom: 20px;
}

.app__right .MuiCardContent-root {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.app__graph {
    flex-grow: 1;
}

@media (max-width: 990px) {
    .app {
        flex-direction: column;
    }
}
